@import url("https://fonts.googleapis.com/css2?family=DM+Serif+Display:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Font+Awesome+5+Brands:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-jost: Jost;
  --font-dm-serif-display: "DM Serif Display";
  --font-font-awesome-5-brands: "Font Awesome 5 Brands";

  /* font sizes */
  --font-size-21xl: 40px;
  --font-size-lg: 18px;
  --font-size-3xl: 22px;
  --font-size-6xl: 25px;
  --font-size-31xl: 50px;
  --font-size-66xl: 85px;
  --font-size-base: 16px;

  /* Colors */
  --color-darkslategray: #292f36;
  --color-white: #fff;
  --color-dimgray: #4d5053;
  --color-burlywood: #cda274;
  --color-gainsboro-100: #e7e7e7;

  /* Gaps */
  --gap-12xl: 31px;
  --gap-2xl: 21px;
  --gap-3xs: 10px;
  --gap-lg: 18px;
  --gap-33xl: 52px;
  --gap-xs: 12px;
  --gap-xl: 20px;
  --gap-6xl: 25px;
  --gap-4xl: 23px;
  --gap-9xs: 4px;
  --gap-5xl: 24px;
  --gap-base: 16px;
  --gap-11xl: 30px;
  --gap-46xl: 65px;
  --gap-2xs: 11px;
  --gap-4xs: 9px;

  /* Paddings */
  --padding-7xl: 26px;
  --padding-35xl: 54px;
  --padding-2xl: 21px;

  /* border radiuses */
  --br-lg: 18px;
  --br-51xl: 70px;
  --br-5xs: 8px;
  --br-43xl: 62px;
}
